body{
  background-color: rgb(189, 137, 252);
  color: white;
  font-family: 'Roboto Mono', monospace;
}
.app-div {
  width: 700px;
  height: 600px;
  background-color: wheat;
  border-radius: 5%;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header-div{
  height: 150px;
  background-color: white;
  color: black
}
.header-div h1{
  margin: 0;
  text-align: center;
  padding-top: 2%;
}
.title-disc{
  font-size: 15px;
}
.title-disc h1{
  margin-top: 0;
  padding-top: 0;
  margin-bottom: 5%;
}

.content-div{
  height: 500px;
  background-color: wheat;
}
.content-div h1{
  margin-top: 0;
  text-align: center;
}
.header-div h2{
  text-align: end;
  padding-right: 5%;
  display: inline-block;
}
.header-title-div{
  align-items: center;
  align-content: center;
}
.logo-img{
  padding-right: 1%;
  padding-top: 1%;
  width: 25px;
}
.datelogo-img{
  width: 30px;
  padding-left: 72%;
}
.date-div{
  max-height: 45px;
  background-color: white;
  overflow: hidden;
  align-items: center;
  display: flex;
}

.content-div{
  align-items: center;
  display: flex;
  flex-direction: column;
  max-height: 350px;
}

.fact-div{
  max-width: 600px;
  min-height: 50px;
  background-color: lightblue;
  text-align: center;
  margin-top: 2%;
  align-items: center;
  padding: 2%;
  align-items: center;
  display: flex;
  border-radius: 2%;
}
.fact-div p{
  margin: 0;
  padding: 0;
}
.fact-btn{
  width: 150px;
  height: 50px;
  border: none;
  border-radius: 10%;
  font-family: 'Roboto Mono', monospace;
  background-color: rgb(181, 226, 235);
  position: relative;
  border-radius: 4em;
  font-size: 16px;
  color: white;
  padding: 0.8em 1.8em;
  cursor:pointer;
  user-select:none;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition-duration: 0.4s;
  -webkit-transition-duration: 0.4s; /* Safari */
}

.fact-btn:hover{
  transition-duration: 0.1s;
  background-color: #3A3A3A;
}

.fact-btn:active:after{
  box-shadow: 0 0 0 0 white;
  position: absolute;
  border-radius: 4em;
  left: 0;
  top:0;
  opacity: 1;
  transition: 0s;
}
.fact-btn:active {
  top: 1px;
}

.fact-btn-div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dev-div{
  text-align: end;
  padding-right: 3%;
  margin-top: 5px;
}
.dev{
  color: rgb(28, 240, 28);
}